import React, { Component } from "react";
import Ad from "./adu/adu";
import EP from "./adu/enterPass";

export default class AuthAdu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isAuth: false,
      realPass: "thisismypornsite",
      enteredPass: "",
      isValid: true,
      isLoading: false
    };
    this.onChange = this.onChange.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  onClick(e) {
    this.setState({ isLoading: true });
    if (this.state.realPass === this.state.enteredPass) {
      this.setState({ isAuth: true });
      return;
    }
    this.setState({ isValid: false, isLoading: false });
  }
  onChange(event) {
    this.setState({ isValid: true });
    this.setState({ [event.target.name]: event.target.value });
  }
  render() {
    return (
      <div>
        {this.state.isAuth ? <Ad></Ad> : null}
        {!this.state.isAuth ? (
          <EP
            valid={this.state.isValid}
            password={this.state.enteredPass}
            onChange={this.onChange}
            onClick={this.onClick}
            isLoading={this.state.isLoading}
          ></EP>
        ) : (
          ""
        )}
      </div>
    );
  }
}
