import React, { Component } from "react";
import LH from "../../header/LoginHeader.js";
// import SContent from "../../sContent/sContent1";
import seducingImages from "../../../strings/SIURL";
export default class Home extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div>
        <LH heading="Home"></LH>
        <div
          style={{
            display: "flex",
            justifyContent: "space-around",
            flexWrap: "wrap",
            // flexFlow: "row wrap",
            // flexDirection: "column",
            marginTop: "5%",
            marginBottom: "5%",
            alignItems: "stretch",
            width: "100"
          }}
        >
          {seducingImages.map(url => (
            <img src={url}></img>
          ))}
        </div>

        <button className="button is-danger comingFromDown">Log Out</button>
      </div>
    );
  }
}

